@import url(https://fonts.googleapis.com/css?family=Heebo&display=swap);
@import url(https://fonts.googleapis.com/css?family=Handlee&display=swap);
body {
  margin: 0;
  font-family: 'Heebo', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.App {
  min-height: 100vh;
  background-color: #f2f2f2; }

.header {
  display: flex;
  flex-direction: column;
  color: black;
  background-color: lightgrey; }
  .header h1 {
    font-size: 4em;
    font-family: 'monospace';
    overflow: hidden;
    /* Ensures the content is not revealed until the animation */
    border-right: .13em solid black;
    /* The typwriter cursor */
    white-space: nowrap;
    /* Keeps the content on a single line */
    margin: 0 auto;
    /* Gives that scrolling effect as the typing happens */
    letter-spacing: .10em;
    /* Adjust as needed */
    -webkit-animation: typing 4.5s steps(35, end), blink-caret 0.65s step-end infinite;
            animation: typing 4.5s steps(35, end), blink-caret 0.65s step-end infinite;
    max-width: 500px;
    padding: 10px 0; }
    @media only screen and (max-width: 600px) {
      .header h1 {
        font-size: 2em;
        max-width: 250px; } }

@-webkit-keyframes typing {
  from {
    width: 0; }
  to {
    width: 100%; } }

@keyframes typing {
  from {
    width: 0; }
  to {
    width: 100%; } }

@-webkit-keyframes blink-caret {
  from, to {
    border-color: transparent; }
  50% {
    border-color: black; } }

@keyframes blink-caret {
  from, to {
    border-color: transparent; }
  50% {
    border-color: black; } }

table .MuiTableCell-sizeSmall {
  padding: 0 10px !important; }

